import MiniCard from "./mini-card"
import logo from './icon/wave.svg'
import { Link, Outlet } from "react-router-dom"


const categoryes = [
    {id: 1, title:'', logo:'./icon/corn.svg', link:'/coffee', content:{
            '':'/corn',
            '':'/capsules',
            '':'/drips',
            '':'/accessory'
        }
    },
    {id: 2, title:'', logo:'./icon/cloth.svg', link:'/clothes', content:{
            '':'/top',
            '':'/bottom'
        }
    },
    {id: 3, title:'', logo:'./icon/other.svg', link:'/other', content:{
            '':'/match',
            '':'/',
            '':'/film'
        }
    },
    {id: 4, title:'', logo:'./icon/basket.svg', link:'/basket', content:{
            ' ':'/personalArea',
            ' ':'/wholesaleCustomers',
            '':'/support'
        }
    }
]

const Navbar = ()=>{ 
    return(
        <>
            <div className="navbar container">
                <Link to=''>
                    <div className="navbar__title">surf coffee<br/>official store</div>
                </Link>

                <MiniCard categoryes={categoryes}/>
                
                <div>
                    <img className="flex__child_logo" src={logo}></img>
                </div>
            </div>
            <Outlet/>
        </>

    )
}

export default Navbar